.edit-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-width: 350px;
  label {
    padding-bottom: 5px;
  }
  input {
    margin-bottom: 5px;
    width: 200px;
    height: 30px;
  }

  button {
    border-radius: 10px;
    outline: none;
    height: 30px;
    width: 120px;
    margin-right: 10px;
    border: none;
    cursor: pointer;
    background-color: #be4145;
  }
}
