.App {
  text-align: center;
  // background-color: #004a7c;
  // background-color: #989ea3;
  background-image: url("https://res.cloudinary.com/artyompogosov/image/upload/v1577786915/samples/xwj8lyyq8vcep3pomvol.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}

a {
  text-decoration: none;
  color: unset;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  padding: 10px;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 80px;
}

// .App-footer{
//     background-color: #282c34;
//     height: 50px;
//     bottom: 0;
// }

.main {
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  font-size: 30px;
}

.nav-logo {
  height: 100%;
  margin-right: 20px;
  display: flex;
  align-items: center;
}
.statistic-logo {
  // width: 30px;
  height: 70%;
}
.container-nav {
  // width: 100%;
  margin: 0 auto;
  width: 90%;
  //   display: flex;

  justify-content: space-between;
  .nav-bar {
    height: 100%;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    justify-content: space-between;
    div {
      height: 100%;
      display: flex;
      align-items: center;
    }
  }
}

@media (max-width: 750px) {
  .container-nav {
    // width: 100%;
    margin: 0 auto;
    width: 100%;
  }

  div {
    .nav-logo {
      margin-right: 5px;
      .statistic-logo {
        width: 40px;
        height: 40px;
      }
    }
  }
}

//homepage

.user-name {
  font-size: 30px;
  margin: 0;
  margin-top: 20px;
}

.moves {
  border-top: gold 0.5px solid;
}
.userDetailsMain {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;
  margin-top: 50px;
  width: 100%;
  .profile-img {
    border-radius: 100%;
    height: 200px;
    border: 1px solid black;
    background-color: white;
  }
  div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: center;
    height: 200px;
    margin-left: 50px;
    // margin-top: 30px;
    h2 {
      margin-top: 0;
      // margin-bottom: 50px;
    }
  }

  h2 {
    display: flex;
    align-items: center;
    img {
      width: 30px;
      margin-right: 10px;
    }
  }
}

.logout {
  float: right;
  border-radius: 10px;
  outline: none;
  padding: 5px;
  margin: 10px;
  cursor: pointer;
  border: none;
  background-color: #be4145;
}

@media (max-width: 500px) {
  .userDetailsMain {
    flex-direction: column;
  }
  .filterArea {
    input {
      margin-left: 20px !important;
    }
    button {
      margin-right: 20px !important;
    }
  }
}

//cobtacts page

.filterArea {
  height: 50px;
  background-color: #292828;
  display: flex;
  justify-content: space-between;
  button {
    border-radius: 10px;
    outline: none;
    height: 70%;
    margin-right: 40px;
    margin-top: 8px;
    cursor: pointer;
    border: none;
    background-color: #be4145;
    color: white;
  }
  input {
    width: 40%;
    background-color: #292828;
    border-radius: 10px;
    outline: none;
    color: white;
    border: none;
    font-size: 16px;
    margin-left: 40px;
  }
  ::placeholder {
    color: rgb(255, 255, 255);
    font-size: 16px;
  }
}

//moves

.moves-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
  padding-left: 0;

  li {
    height: 40px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
    border-bottom: none;

    min-width: 300px;
  }
}

//canvas

.canvas {
  width: 80%;
  margin: 0 auto;
  div {
    margin-bottom: 20px;
  }
}
