ul {
  li {
    display: flex;
    // margin: 10px;
    padding: 20px 0;

    align-items: center;
    width: 70%;
    height: 100%;
    min-width: 350px;
    border-bottom: gold 1px solid;
    box-shadow: 0px 15px 10px -15px #111;
    a {
      // height: 100%;
      width: 100%;
    }

    .cats-img {
      max-width: 200px;
      // min-width: 100px;
      max-height: 200px;
      border-radius: 100%;
      margin-left: 3%;
      margin-right: 30px;
      border: 1px solid black;
      background-color: rgb(255, 255, 255);
    }
    h2 {
      color: black;
    }
  }
}

// ul {
//   width: 100%;
// }

.contactPrev {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.contact-details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-content: center;
  justify-content: center;
}
@media (max-width: 530px) {
  .cats-img {
    margin-left: 30px;
    width: 130px;
    height: 130px;
  }
  .contact-details {
    margin-right: 7% !important;
    h2 {
      font-size: 16px;
      margin: 5px 0;
    }
    h3 {
      font-size: 15px;
      margin: 5px 0;
    }
  }
}

@media (max-width: 420px) {
  .cats-img {
    margin-left: 10px !important;
    margin-right: 5px !important;
  }
  .contact-details {
    margin-right: 3% !important;
  }
}

@media (max-width: 750px) {
  .cats-img {
    margin-right: 0px;
    margin-left: 10%;
    height: 100%;
  }
  .robot-list {
    padding: 0;
    margin: 0;
  }

  ul {
    li {
      width: 100%;
      height: 70%;
    }
  }
  .contactPrev {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .contact-details {
    margin-right: 10%;
    h2 {
      margin: 5px 0;
    }
    h3 {
      margin: 10px 0;
    }
  }
}
