.contact-full-details {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
  margin-bottom: 30px;
  img {
    border: 1px solid black;
    background-color: white;
    margin-top: 20px;
    width: 200px;
    margin-right: 50px;
    border-radius: 100%;
  }
}

.details-btn {
  button {
    border-radius: 10px;
    outline: none;
    height: 30px;
    width: 60px;
    margin-right: 10px;
    border: none;
    background-color: #be4145;
  }
}

.Transfer-btn {
  border-radius: 10px;
  outline: none;
  width: 80px;
  height: 30px;
  cursor: pointer;
  border: none;
  background-color: #be4145;
}

@media (max-width: 550px) {
  .contact-full-details {
    flex-direction: column-reverse;
    img {
      margin-right: 0px !important;
    }
  }

  .contact-details {
    h1 {
      margin: 10px 0;
    }
  }
}

@media (max-width: 750px) {
  .contact-full-details {
    margin-bottom: 20px;

    justify-content: center;
    img {
      height: 200px;
      width: 200px;
      margin-right: 50px;
    }
  }
  .contact-details {
    margin-right: 0;
  }
}
